@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  padding: 0;
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #888;
}

.home {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: rgb(49, 47, 42);
}
.nav {
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  height: 40px;
  background-color: rgb(39, 36, 35);
}
.login {
  display: flex;
  align-items: center;
}
.login input {
  color: white;
  background-color: rgb(61, 59, 57);
  height: 25px;
  margin-left: 3px;
  border-radius: 2px;
  border: 0;
}
.login input::-webkit-input-placeholder {
  color: white;
  opacity: 0.6;
}
.login input:-ms-input-placeholder {
  color: white;
  opacity: 0.6;
}
.login input::placeholder {
  color: white;
  opacity: 0.6;
}

.login button {
  height: 30px;
  width: 100px;
  margin-right: 10px;
  border: 0;
  border-radius: 6px;
  background-color: forestgreen;
  color: white;
  opacity: 0.8;
  transition: 0.3s;
}
.login button:hover {
  opacity: 1;
}

.logininfo button {
  height: 30px;
  width: 100px;
  margin-right: 10px;
  border: 0;
  border-radius: 6px;
  background-color: forestgreen;
  color: white;
  opacity: 0.8;
  transition: 0.3s;
}
.logininfo button:hover {
  opacity: 1;
}
.logininfo img {
  height: 40px;
  width: 40px;
  padding-right: 10px;
}
.logo img {
  height: 60px;
  width: 60px;
  padding-right: 10px;
}
.logo {
  display: flex;
  align-items: center;
}
.logininfo {
  display: flex;
  align-items: center;
}
/* ---Content--- */
.content {
  margin: auto;
  display: grid;
  max-width: 1612px;
  grid-template-rows: 30px 600px;
  grid-template-columns: 58px 600px 58px 400px 58px 300px 58px;
  height: calc(100vh - 40px);
}
.homeBoard {
  grid-column: 2/3;
  grid-row: 2/3;
}

.login_error {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: auto;
  height: 60px;
  background-color: rgb(218, 40, 40);
  color: white;
  border-radius: 10px;
}

.login_error img {
  width: 35px;
  height: 35px;
  padding: 8px;
}

.challenger {
    display: flex;
    align-items: center;
    padding: 10px;
    position: absolute;
    right: 90vh;
    width: 300px;
    height: 60px;
    background-color: rgb(21, 116, 21, 0.5);
    color: white;
    border-radius: 10px;
    z-index: 1000;
  }
  .challenger button {
    margin-left: auto;
    margin-right: 15px;
    background: transparent;
    color: rgb(255, 217, 0);
    border: 0;
    cursor: pointer;
  }
  .challenger button:hover {
    text-decoration: underline;
  }

.online {
    grid-column: 4/5;
    grid-row: 2/3;
    background: #212020;
    width: 400px;
  }
  
  .friends {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 400px;
  }
  .friend {
    display: flex;
    align-items: center;
    padding: 5px;
    color: white;
  }
  .friend img {
    height: 47px;
    width: 47px;
    padding-right: 10px;
  }
  .friend button {
    margin-left: auto;
    margin-right: 15px;
    color: white;
    width: 100px;
    height: 40px;
    background: #412086;
    border: 0;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.2s;
  }
.title {
    margin: 10px;
    color: white;
    text-align: center;
  }
  
.computer a {
    margin-left: auto;
    margin-right: 15px;
    color: white;
    width: 100px;
    height: 40px;
    background: #412086;
    border: 0;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.2s;
  }
.GameSettings {
    height: 600px;
    width: 300px;
    grid-column: 6/7;
    grid-row: 2/3;
    background: #212020;
}
.GameSettings h2 {
    color: white;
    text-align: center;
    margin: 10px;
}
.GameSettings h3 {
    color: white;
    margin: 10px;
}
.TimeControl {
    display: flex;
}
.TimeControl input {
    background: #383635;
    border: 0;
    margin-left: 10px;
    width: 60px;
    height: 40px;
    color: white;
}
.TimeControl input::-webkit-input-placeholder {
    padding-left: 5px;
}
.TimeControl input:-ms-input-placeholder {
    padding-left: 5px;
}
.TimeControl input::placeholder {
    padding-left: 5px;
}
.GameSettingsInput {
    display: flex;
    flex-direction: column;
}
.GameSettingsInput p {
    color: white;
    margin-left: 10px;
}
.container {
    background-color: rgb(30, 34, 34);
    height: 725px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .board-container p {
    color: white;
    align-self: flex-start;
  }
  .board-container {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .playerInfo p {
    align-self: flex-end;
  }
  .playerInfo {
    align-self: flex-start;
    display: flex;
  }
  .playerInfo img {
    height: 40px;
    width: 40px;
    padding-right: 10px;
  }
  .player {
    display: flex;
    width: 600px;
  }
  .draw {
    cursor: pointer;
    align-self: flex-end;
    height: 30px;
    width: 100px;
    margin-left: 65px;
    border: 0;
    border-radius: 6px;
    background-color: forestgreen;
    color: white;
    opacity: 0.8;
    transition: 0.3s;
  }
  .draw:hover {
    opacity: 1;
  }
  .gameback {
    position: absolute;
    top: 50px;
    left: 50px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    background: transparent;
    border: 0;
  }
  .gameback:hover {
    text-decoration: underline;
  }
  .draw_offer {
    display: flex;
    align-items: center;
    padding: 10px;
    position: absolute;
    right: 90vh;
    top: 20px;
    width: 300px;
    height: 60px;
    background-color: rgb(21, 116, 21, 0.5);
    color: white;
    border-radius: 10px;
    z-index: 1000;
  }
    

.result {
    font-weight: 900;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100px;
    width: 200px;
    background-color: white;
    color: black;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
      rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
      rgba(0, 0, 0, 0.09) 0px 32px 16px;
    border-radius: 16px;
    z-index: 1000;
  }
  .result button {
    margin-top: 8px;
    height: 25px;
    width: 80px;
    border: 0;
    border-radius: 6px;
    background-color: forestgreen;
    color: white;
  }
.timer {
  margin-left: auto;
}


.register_page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
  }
  .register {
    position: relative;
    z-index: 1;
    background: #ffffff;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  }
  .register input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
  }
  .register button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #4caf50;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #ffffff;
    font-size: 14px;
    transition: all 0.3 ease;
    cursor: pointer;
  }
  .register button:hover,
  .register button:active,
  .register button:focus {
    background: #43a047;
  }
  
  
.computer_board {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
  grid-gap: 100px;
  gap: 100px;
}

.config {
  height: 600px;
  width: 300px;
  color: white;
  background-color: #212020;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.config h2 {
  align-self: flex-start;
  margin: 10px;
}

.btn {
  margin-bottom: 15px;
  color: white;
  width: 100px;
  height: 40px;
  background: #412086;
  border: 0;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.2s;
}

